<template>
    <div class="login-page flex">
        <div class="col-main">
            <div class="logo">
                <img src="@/assets/logo_title.png">
            </div>
            <div class="slide-section">
                <el-carousel direction="vertical" :autoplay="false">
                    <el-carousel-item class="flex flex-align-center flex-pack-center" v-for="(item,index) in carouselList" :key="index">
                        <div>
                            <div class="title">
                                <h2>{{item.title}}</h2>
                                <small>{{item.desc}}</small>
                            </div>
                            <div class="item-slide">
                                <img :src="item.src">
                            </div>
                        </div>
                    </el-carousel-item>
                </el-carousel>
            </div>
        </div>
        <div class="col-sub">
            <div class="form-login">
                <h2 class="title">登录</h2>
                <el-form ref="loginForm" :model="loginForm" :rules="rules" label-width="0px">
                    <el-form-item prop="username">
                        <el-input v-model="loginForm.username" prefix-icon="el-icon-user" placeholder="用户名"></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" v-model="loginForm.password" prefix-icon="el-icon-lock" placeholder="密码"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button style="width:100%" type="primary" @click="doLogin">登录</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="copyright">
                <p>{{ $store.state.settings.footerTxt }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import Config from '@/config'
import Cookies from 'js-cookie'
import '@/styles/login.scss'
import imgSrc1 from "@/assets/img_banner_1.png";
import imgSrc2 from "@/assets/img_banner_2.png";
import imgSrc3 from "@/assets/img_banner_3.png";
import qs from 'qs'
export default {
    data(){
        return{
            copyrightYear: '',
            carouselList: [
                {
                    title: '互联网+农业',
                    desc: '以创新驱动农业现代化发展',
                    src: imgSrc1
                },
                {
                    title: '安全 高效 标准化',
                    desc: '随时随地掌控生产情况',
                    src: imgSrc2
                },
                {
                    title: '安全 高效 标准化',
                    desc: '随时随地掌控生产情况',
                    src: imgSrc3
                }
            ],
            loginForm: {
                username: '',
                password: ''
            },
            rules: {
                username: [
                    { required: true, message: '请输入用户名', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ]
            },
            loading: false
        }
    },
    methods:{
        getCookie() {
            const username = Cookies.get('username')
            let password = Cookies.get('password')
            // 保存cookie里面的加密后的密码
            this.cookiePass = password === undefined ? '' : password
            password = password === undefined ? this.loginForm.password : password
            this.loginForm = {
                username: username === undefined ? this.loginForm.username : username,
                password: password,
            }
        },
        doLogin(){
            this.$refs.loginForm.validate(valid => {
                if (valid) {
                    this.loading = true
                    this.$store.dispatch('Login', qs.stringify(this.loginForm)).then(() => {
                        this.loading = false
                        this.$router.push('/')
                    }).catch(() => {
                        this.loading = false
                    })
                } else {
                    return false
                }
            })
        }
    },
    created(){
        
    }
}
</script>

<style>

</style>


